import React,{Component} from 'react';
import './index.css';
import Orientation from "../../orientation.json";
class Header extends Component{
    randomColorGenerator(){
        /* var hexStr = "ABCDEF0123456789";
        var hexStr1 = "0123456789";
        var hexColor = "#";
        for(var i = 0; i<6;i++){
            if(i===0){
                hexColor += hexStr1.charAt(Math.floor(Math.random()*6));
            }else{
                hexColor += hexStr.charAt(Math.floor(Math.random()*6));
            }
        } */
        if(localStorage.getItem("navbar")){
            return localStorage.getItem("navbar");
        }else{
        var rgb = "rgb(";
        for(var i = 0; i<3;i++){
            if(i === 2){
                rgb += (Math.floor((Math.random() * 150))).toString()+")"
            }else{
                rgb += (Math.floor((Math.random() * 150))).toString()+","
            }
        }
        //localStorage.setItem("navbar",rgb);
        return rgb;
        }
    }
    render(){
        return(
        <header className="header" style={{background:"linear-gradient(to left,"+this.randomColorGenerator()+","+this.randomColorGenerator()+","+this.randomColorGenerator()+")"}}>
            <div className="container">
            <div className="mobileNotifications" onClick={(e)=>{
                if(document.getElementById("mobileMenu").classList.contains("slideInRight")){
                    document.getElementById("mobileMenu").classList.remove("slideInRight");
                    document.getElementById("mobileMenu").classList.add("slideOutRight");
                }
                if(document.getElementById("mobileNotificationContainer").classList.contains("slideOutDown")){
                    document.getElementById("mobileNotificationContainer").classList.remove("slideOutDown");
                    document.getElementById("mobileNotificationContainer").classList.add("slideInUp");
                    if(document.getElementById("mobileNotificationContainer").style.display === "none"){
                        document.getElementById("mobileNotificationContainer").style.display = "block";
                    }
                }else if(document.getElementById("mobileNotificationContainer").classList.contains("slideInUp")){
                    document.getElementById("mobileNotificationContainer").classList.remove("slideInUp");
                    document.getElementById("mobileNotificationContainer").classList.add("slideOutDown");
                    //document.getElementById("mobileMenu").style.display = "block";

                }
            }}>
                <i className="fa fa-bell fa-2x mobileNotificationsIcon"></i>
            </div>
            <img className="logo" src="https://images.mixer.com/sOtPOPTJYMy_SY-A4Qblfp48Lx-IqVKtoyIW_v7VJHYM/https://i.imgur.com/KJ8fLiD.png" width="auto" height="55px" alt="" />
            <div className="mobileMenu" onClick={(e)=>{
                if(document.getElementById("mobileMenu").classList.contains("slideOutRight")){
                    if(document.getElementById("mobileNotificationContainer").classList.contains("slideInUp")){
                        document.getElementById("mobileNotificationContainer").classList.remove("slideInUp");
                        document.getElementById("mobileNotificationContainer").classList.add("slideOutDown");
                    }
                    document.getElementById("mobileMenu").classList.remove("slideOutRight");
                    document.getElementById("mobileMenu").classList.add("slideInRight");
                    if(document.getElementById("mobileMenu").style.display === "none"){
                        document.getElementById("mobileMenu").style.display = "block";
                    }
                }else if(document.getElementById("mobileMenu").classList.contains("slideInRight")){
                    document.getElementById("mobileMenu").classList.remove("slideInRight");
                    document.getElementById("mobileMenu").classList.add("slideOutRight");
                    //document.getElementById("mobileMenu").style.display = "block";

                }
            }}>
                <i className="fa fa-bars fa-2x mobileMenuBarIcon"></i>
            </div>
            <ul className="menuItems animated heartBeat">
                {Orientation[0].menuItems.map(function(data,index){
                  return(<li key={index}>{data}</li>);
                })}
                {Orientation[0].buttons.map(function(data,index){
                  return(<li style={{color:"black",}} key={index}><button style={{background:"yellow",outline:"none",border:"2px solid white",height:"30px",fontSize:"15px",minWidth:"40px",fontWeight:"bold",fontFamily:"'Maven Pro', sans-serif"}}>{data}</button></li>);
                })}
            </ul>
            <div className="mobileMenuContainer animated faster slideOutRight" style={{display:'none'}} id="mobileMenu"></div>
            <div className="mobileNotificationContainer animated faster slideOutDown" style={{display:'none'}} id="mobileNotificationContainer"></div>
            </div>
        </header>
        );
    }
}
export default Header;