import React,{Component} from 'react';
/* import Orientation from '../../orientation.json'; */
import LeftScript from "../LeftScript/index.js";
import "./index.css";
class Parallax extends Component{
    randomColorGenerator(){
        if(localStorage.getItem("navbar")){
            return localStorage.getItem("navbar");
        }else{
        var rgb = "rgb(";
        for(var i = 0; i<3;i++){
            if(i === 2){
                rgb += (Math.floor((Math.random() * 150))).toString()+")"
            }else{
                rgb += (Math.floor((Math.random() * 150))).toString()+","
            }
        }
        //localStorage.setItem("navbar",rgb);
        return rgb;
        }
    }
    render(){
        return (
            <div className="parallax" style={{backgroundColor:this.randomColorGenerator()}}>
                <LeftScript />
            </div>
        );
    }
}
export default Parallax;