import React,{Component} from 'react';
import "./index.css";
import Orientation from "../../orientation.json";
class LeftScript extends Component{
    randomColorGenerator(){
        if(localStorage.getItem("navbar")){
            return localStorage.getItem("navbar");
        }else{
        var rgb = "rgb(";
        for(var i = 0; i<3;i++){
            if(i === 2){
                rgb += (Math.floor((Math.random() * 150))).toString()+")"
            }else{
                rgb += (Math.floor((Math.random() * 150))).toString()+","
            }
        }
        //localStorage.setItem("navbar",rgb);
        return rgb;
        }
    }
    render(){
        const container = {
            minWidth:"250px",
            maxWidth:"800px",
            width:"30%",
            height:"100%",
            position:"relative",
            zIndex:10,
            top:"0px",
        }
        const opacContainer = {
            opacity:"0.8",
            backgroundColor:"black",
            width:"100%",
            position:"relative",
            top:"0px",
            height:"100%"
        }
        const container2 = {
            height:"calc(100% - 20px)",
            padding:"10px",
            width:"100%",
            position:"relative",
            left:"50%",
            top:"-50%",
            transform:"translateX(-50%) translateY(-50%)"
        }
        return(
            <div style={container} className="leftScript animated slideInLeft">
                <div style={opacContainer}></div>
                <div style={container2}>
                <div className="imageCon">
                    <img src="./image/ablam.png" width="100%" height="auto" alt="" />
                </div>
                <div className="lstitle">
                    Gökçe Hız
                    <br />
                    <div style={{fontSize:"18px"}}>Psikoloji Danışmanı</div>
                    <hr style={{borderWidth:"0.5px"}}/>
                </div>
                <div className="lsdescription" dangerouslySetInnerHTML={{__html:Orientation[0].descriptionTop}}>      
                </div>
                </div>
            </div>
        );
    }
}
export default LeftScript;